         <b-row>
        <b-col cols='8' offset = '2' class='next-slide-container'>
          <div class="next-slide-text">
            The early days of World War II saw the establishment of a mobile medical unit by trial and error by Major Vincent Marran. Although the effectiveness of his efforts were admired and encouraged by command, there were no attempts to establish formal designation. 
            <br><br>
            The idea of a Mobile Army Surgical Hospital (MASH) unit was formally conceived by Michael DeBakey. It was to be a better alternative as compared to the random individual portable surgical hospitals and field hospitals systems that was widely used during World War II. Since treatment capability and time are crucial factors for the survival of casualties, the aim was to treat casualties as soon as possible with experienced personnel closer to the front. MASH was designed with that reasoning in mind. The first point of contact of battlefield medical management for casualties was through buddy aid. Casualties were then transferred to Battalion Aid Stations for emergency stabilization surgery and triage, before finally being evacuated to MASH for more extensive treatment.
            <br><br>
            MASH units saw its first major deployment during the Korean war. Since then, they have seen action in every major conflict the United States was involved in up until 2006, after which it was replaced by the Combat Support Hospital. Its main mission was to function as a sixty bed surgical hospital. However, in many instances the unit had to assume additional responsibilities of an evacuation hospital without compromising operational efficiency. As MASH units were usually located close to the frontlines, personnel were in no less danger from enemy raids as compared to frontline combat units.
            <br><br>
            The term MASH was made famous in movies and the subsequent television series M*A*S*H which depicts life at the fictional 4077th that is loosely based on the 8055th. 

          </div>
        </b-col>
      </b-row>

      <b-row>
        <b-col cols='8' offset = '2' class='next-slide-container'>
          <div class="next-slide-text">
            <h4>Evolution of Battlefield Medicine</h4><br>

            The Korean War saw many firsts. Major advancements in casualty transport and medical procedure occurred during the war. Evacuation by air was initially in the Air Force’s sphere of responsibility as they used large cargo planes to transport patients to the rear. By the start of the Korean War, helicopters or ‘air ambulance’ were introduced and favoured for its quick and relative ease of casualty transportation from the frontlines. The Bell H-13 was the main helicopter used for medical evacuation (MEDEVAC) as it was the first certified helicopter. Nicknamed the ‘Angel of Mercy’, the Bell H-13 was responsible for evacuating some 18,000 United Nations casualties (Booker & Ismail, 2005, p. 650) . However, since only two patients at a time could be transported via the skids placed outside each helicopter, this meant that the treatment that each patient received during transport was limited. Evacuation by air undoubtedly was a main factor in the significant reduction in the mortality rate of wounded soldiers during the Korean war conflict as compared with previous conflicts. 
            <br><br>
            Although the idea of triage is not new during the Korean War, there was substantial modification to the concept during the conflict. Triage was instituted at battalion aid stations which were small medical units that had limited capabilities. These stations were capable of supporting up to 1,000 soldiers. Here, general medical officers and nurses evaluated a soldier’s condition and wound severity. Patients needing surgery and all critical patients were rapidly evacuated to the MASH by helicopter. They were further evaluated based on their hemodynamic status and injury. Injuries deemed most critical were prioritized for surgery (Booker & Ismail, 2005, p. 650 - 651). Owing to the huge casualty influx at most if not all MASH hospitals, there was a need to efficiently allocate resources to where it is needed most. This meant that patients considered to unlikely survive were managed expectantly. If patients require specialised therapy such as dialysis and neurosurgery, they were then evacuated to specialty centers further back from the front line.
            <br><br>
            Triaging at MASH units was modeled after the dictum: "Life takes precedence over limb, function over anatomical defects." 
            <br><br>
            In the course of saving lives comes with improvements in areas such as anesthesia and perioperative care. These improvements were based on the collective ground staff experience various MASH units. The practise of resuscitating casualties with crystalloid was not formally implemented until the Vietnam War (Booker & Ismail, 2005, p. 652). Before the Vietnam War, patients whose conditions were unstable were transfused whole blood. While this was effective in managing the condition for some patients, it was not entirely foolproof. Acute renal failure occurred to 0.5% of frontline casualties and needless to say, such a condition in this rough and unforgiving setting resulted in high mortality rates of about 80% to 90% despite immediate care. To combat this, anesthesiologists started using minute amounts of narcotic for induction. Chloroform and ether were also discontinued as anesthetics as it was found to be detrimental to the patients health due to their negative inotropic effects and replacing chloroform and ether was nitrous oxide used Other anesthesia that was recommended for use by MASH units were Thiopental for induction and Tubocurarine and succinylcholine for rapid intubation. 

          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols='8' offset = '2' class='next-slide-container'>
          <div class="next-slide-text">
            <h4>MASH Living Conditions</h4><br>

            The popular 1970s M*A*S*H television series was based off Richard Hooker's experience during his time as a surgeon in the Korean War. Living conditions depicted in the series appeared harsh and spartan. That depiction was far from reality and settling conditions in an actual MASH unit were far worse. MASH personnel had to endure extremely low temperature and live off the rugged Korean mountainous terrain (Booker & Ismail, 2005, p. 652). As MASH units were assigned to infantry divisions, they had to move multiple times each month to keep up with the advancing combat units. Medical staff worked around the clock to treat and care for the large casualty influx while surgeons operated continuously with little rest. In fact, it comes as no surprise that some MASH units had a monthly admission of close to over 3,000 casualties. In other words, 200 bed MASH units often had to treat 400 patients per day. The close proximity at which MASH was located, sometimes just 10 miles behind the front lines, made them vulnerable to artillery bombardment and enemy attacks. These were the conditions that MASH staff endured for the most part of 1950 and 1951 (Booker & Ismail, 2005, p. 652). By late 1953, with the war winding down, only six MASH units were left in Korea and they were re-tasked to provide treatment for prisoners of war (POW) and civilian casualties. 

          </div>
        </b-col>
      </b-row>
