            <h4>Depiction of MASH in Popular Culture</h4><br>

            The popular 1970s M*A*S*H television series was based off Richard Hooker's experience during his time as a surgeon in the Korean War. Living conditions depicted in the series appeared harsh and spartan. That depiction was far from reality and settling conditions in an actual MASH unit were far worse. MASH personnel had to endure extremely low temperature and live off the rugged Korean mountainous terrain (Booker & Ismail, 2005, p. 652). As MASH units were assigned to infantry divisions, they had to move multiple times each month to keep up with the advancing combat units. Medical staff worked around the clock to treat and care for the large casualty influx while surgeons operated continuously with little rest. In fact, it comes as no surprise that some MASH units had a monthly admission of close to over 3,000 casualties. In other words, 200 bed MASH units often had to treat 400 patients per day. The close proximity at which MASH was located, sometimes just 10 miles behind the front lines, made them vulnerable to artillery bombardment and enemy attacks. These were the conditions that MASH staff endured for the most part of 1950 and 1951 (Booker & Ismail, 2005, p. 652). By late 1953, with the war winding down, only six MASH units were left in Korea and they were re-tasked to provide treatment for prisoners of war (POW) and civilian casualties. 

          </div>
