          <div class="next-slide-text">
            <h4>Women in MASH</h4><br>

            There is a brief scene in the opening credits of M*A*S*H that depicts a group of nurses running towards the helicopter pad. When the television series was aired, many viewers were confused as to why women were involved during the war. Though they were not directly participate in combat, these brave women were the closest to the fighting front in the history of America and they saved countless lives during their service (Cowdrey, 1987, p. 130). Owing to their close proximity to the fight, they had to do more than regular nurses - from duties such as blood transfusions, administering antibiotics, triaging and suturing of wounds. They even treated civilians caught in the crossfire. Sufficient to say, a MASH unit would not be complete without its nurses. Living near the front meant that every comfort which women were thought to need had to be forsaken. They had to make do with oversized male army fatigues and boots, rough living conditions and a highly stressful workplace. However, these women were not the faint of heart for they have undergone three years of gruelling training in nursing school and graduated with the rank of lieutenant or captain (W. L. Whitefrom, 2005, p. 67).
            <br><br>
            Apart from external inconveniences and rough living conditions, the nurses also had to take care against the patients that they are taking care of. Wounded soldiers would often attempt to harass the nurses and this was so much an issue that sentries had to be posted each night outside the nurse's tent for protection. However, that is not to say that the nurses could not take care of themselves. Ironically, most of these nurses outrank most of the men harassing them.
            <br><br>
            By the end of the Korean War, an estimated 1,500 nurses served their tours of duty in the Korean peninsula. These nurses went on to receive 9 Legions of Merit, 173 Commendation Ribbons and 120 Bronze Star, an impressive feat that exemplifies their courageous and selfless work during the course of their service (Tortorice, 2017).

          </div>
        </b-col>
      </b-row>

      <b-row>
