          <div class="next-slide-text">
            <h4>Evolution of Battlefield Medicine</h4><br>

            The nature of modern warfare and advances in technology has resulted in MASH units to be broken into smaller and more agile medical units. These medical units are now capable of travelling to the front lines to stabilize casualties after a battle before sending them to the rear for proper treatment. This greatly improves the chances of survival of a casualty as he no longer has to spend valuable time waiting to be transported. The Singapore Armed Forces (SAF) for example has a comprehensive multi layer battlefield management system. This five step system ensures that casualties are taken care of at every possible juncture and maximises the chances of survival and recovery.
            <br>

            <div class='ls-header'>1. Frontline Combat Medic</div>
            The frontline combat medic is the first point of contact for a casualty. He is trained and equipped with various medical equipment like chest seals and intravenous fluids to keep the casualty alive in the field before the casualty reaches the next point of contact. The combat medic also conducts triage in conjunction with the company medic. The company medic manages all combat medics. Each rifle platoon has a frontline combat medic attached.<br>

            <div class='ls-header'>2. Battalion Casualty Station (BCS)</div>
            The BCS is a mobile field hospital installed atop a 5 tonner truck and is equipped with Advanced Trauma Life Support. It’s job is to care for all casualties that the battalion sustains after a mission. The BCS is capable of an intake of roughly ____. Each battalion has a BCS attached.<br>

            <div class='ls-header'>3. Medical Company/Mobile Surgical Team</div>
            These surgical units in the field are capable of performing damage control surgery within two hours of sustaining heavy injury. These units have an operating theatre that is modular and high dependency unit. Each brigade has a Medical Company attached.

            <div class='ls-header'>4. Combat Support Hospital (CSH)</div>
            The CSH is run by specialists from multiple disciplines and is made up of mobile containers and tents that can be scaled and are climate-controlled. Signal telecommunications allows for medical information to be exchanged between teams in the field and specialists in the CSH so that the specialists know what to expect when the casualty is received. The CSH has similar functions with MASH.

            <div class='ls-header'>5. Base Hospital</div>
            Hospital staff is in charge of the casualty treatment and rehabilitation upon arriving at the base hospital.

            <div class='ls-header'>6. Land & Aeromedical Evacuation</div>
            All combat ambulances are equipped with the necessary life sustaining, communication and navigational systems onboard as it transfers the casualties between each stage of care. Casualties that are critically wounded are evacuated straight to the CSH via the Chinook CH-47 helicopter. The Chinook has the Air Mobile Life Support Unit II installed onboard and it allows flight medics to perform Intensive Care Unit-type procedures and monitoring during transportation.

          </div>
        </b-col>
      </b-row>

